import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
// import {AllTrendMoviesComponent} from './all-trend-movies/all-trend-movies.component';
import {DaliyTrendingMoviesComponent}from './daliy-trending-movies/daliy-trending-movies.component'
import {WeeklyTrendingMoviesComponent} from './weekly-trending-movies/weekly-trending-movies.component'
import {WeeklyTrendingSeriesComponent} from './weekly-trending-series/weekly-trending-series.component'
import { from } from 'rxjs';
import { DailyTrendingSeriesComponent } from './daily-trending-series/daily-trending-series.component';
import { WeeklyTrendingPersonsComponent } from './weekly-trending-persons/weekly-trending-persons.component';
import { DailyTrendingPersonsComponent } from './daily-trending-persons/daily-trending-persons.component';
import { AllTrendingComponent } from './all-trending/all-trending.component';
import { MovieDetailsComponent } from './movie-details/movie-details.component';
import { TvDetailsComponent } from './tv-details/tv-details.component';
import { PersonDetailsComponent } from './person-details/person-details.component';
import { SearchComponent } from './search/search.component';
import { GetSearchComponent } from './get-search/get-search.component';

const routes: Routes = [
  {path:"",redirectTo:'home',pathMatch:'full'},
  {path:"home",component:AllTrendingComponent},
  {path:"d-t-movies",component:DaliyTrendingMoviesComponent},
  {path:"w-t-movies",component:WeeklyTrendingMoviesComponent},
  {path:"w-t-series",component:WeeklyTrendingSeriesComponent},
  {path:"d-t-series",component:DailyTrendingSeriesComponent},
  {path:"w-t-persons",component:WeeklyTrendingPersonsComponent},
  {path:"d-t-persons",component:DailyTrendingPersonsComponent},
  {path:"moviedetails/:id",component:MovieDetailsComponent},
  {path:"tvdetails/:id",component:TvDetailsComponent},
  {path:"persondetails/:id",component:PersonDetailsComponent},
  {path:"Search/:box.value",component:SearchComponent},
  {path:"getSearch/:id",component:GetSearchComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
