import { Component, OnInit } from '@angular/core';
import {MoviesService} from '../movies.service';
import * as $ from 'jquery' ;
import { from } from 'rxjs';
@Component({
  selector: 'app-all-trending',
  templateUrl: './all-trending.component.html',
  styleUrls: ['./all-trending.component.scss']
})

export class AllTrendingComponent implements OnInit {
  value = '';
  moviesId;
  update(value: string) { 
    this.value = value;
    console.log(this.value);
  }
  
  movies=[];
  series=[];
  persons=[];
  getImg="https://image.tmdb.org/t/p/w500";
  voting=[];
  voteFrom100=[];
  movieId=[];
  wscroll;
  searchMovie;
  valueAfter;
  constructor(public _MoviesService:MoviesService) {
this.valueAfter=this.value;
console.log(this.valueAfter)
    _MoviesService. getSearchResult(this.valueAfter).subscribe((data)=>{
      console.log(data);
    })
  _MoviesService.getTrendingMovies().subscribe( (data)=>{
   this.movies=data.results;
   console.log(this.movies);

  },(error)=>{console.log(error)})

  _MoviesService.getTrendingPersons().subscribe((data)=>{
this.persons=data.results;

  },(error)=>{console.log(error)})

  
_MoviesService.getTrendingSeries().subscribe((data)=>{
  this.series=data.results
  console.log(this.series)
},(error)=>{
  console.log(error)
})


   }
  

  ngOnInit(): void {
    let arrow = document.getElementById("arrow");
    
  let trendingScroll=  $("#trending").offset().top;
    
    arrow.addEventListener("click",function(){
      $("html,body").animate({scrollTop:trendingScroll},500)
      $("nav").slideUp(500)
    })

    window.addEventListener("mousemove",function(e){
      if(e.clientY<144){
        $("nav").slideDown(500)
      }
    })
    
    $(document).ready(function(){
      $(".loading").slideUp(800,function(){
        $("body").css("overflow","auto")
      })
    }) 
  }
}
