import { Component, OnInit } from '@angular/core';
import {ActivatedRoute} from '@angular/router'
import {MoviesService} from '../movies.service'
@Component({
  selector: 'app-tv-details',
  templateUrl: './tv-details.component.html',
  styleUrls: ['./tv-details.component.scss']
})
export class TvDetailsComponent implements OnInit {
id:any;
tvDetails:any;
getImg="https://image.tmdb.org/t/p/w500";
genere=[];
production_companies=[];
last_episode_to_air;
next_episode_to_air;
number_of_episodes;
number_of_seasons;
seasons=[];
  constructor(_ActivatedRoute:ActivatedRoute,_MoviesService:MoviesService) {
    this.id=_ActivatedRoute.snapshot.paramMap.get('id');
    _MoviesService.getTvDetails(this.id).subscribe((data)=>{
      this.tvDetails=data;console.log( this.tvDetails);
      this.genere=data.genres;
      this.seasons=data.seasons;
      console.log(this.seasons);
      this.production_companies=data.production_companies;
    })
   }

  ngOnInit(): void {
  }

}
