import { Component, OnInit } from '@angular/core';
import {MoviesService} from '../movies.service';
import {ActivatedRoute}from  '@angular/router'
import { ValueConverter } from '@angular/compiler/src/render3/view/template';
import * as $ from 'jquery' ;
@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})

export class SearchComponent implements OnInit {
  value;
  searchData=[];
  title=[];
  poster_path=[];
  getImg="https://image.tmdb.org/t/p/w500";
  constructor(public _MoviesService:MoviesService ,_ActivatedRoute:ActivatedRoute) {
    this.value= _ActivatedRoute.snapshot.paramMap.get('box.value');   
    _MoviesService.getSearchResult(this.value).subscribe((data)=>{
      this.searchData=data.results;
      console.log(this.searchData)
     
     
    })

  }

  ngOnInit(): void {
  
    


     let overview= document.getElementsByClassName("overview")

     function myfunc() {
      $(".overview").slideDown(300);
    }

  for(let i =0;i<overview.length;i++){
    overview[i].addEventListener("mouseover",myfunc)
  }



    
  }

}
